import * as React from "react";
import {DashboardContext} from "../../../Providers";
import css from "./NodeCardTransition.module.scss";
import ReactCSSTransitionGroup from "react-addons-css-transition-group";
import ReactDOM from "react-dom";

interface OwnProps {
  children: ({
    onCardSettingsOpenCloseAnimation,
    isNodeSummaryOpen,
    isNodeSettingsloaded,
    getTranslationDistance,
    onNodeSettingsloaded
  }: {
    onNodeSettingsloaded: () => void;
    isNodeSummaryOpen: boolean;
    isNodeSettingsloaded: boolean;
    getTranslationDistance: (index: number) => number;
    onCardSettingsOpenCloseAnimation: () => void;
  }) => React.ReactNode;
}

const ownState = {
  isNodeSummaryOpen: false,
  isNodeSettingsloaded: false
};

export class NodeCardTransition extends React.Component<
  OwnProps,
  typeof ownState
> {
  readonly state = ownState;

  static contextType = DashboardContext;
  context!: React.ContextType<typeof DashboardContext>;
  componentDomRef: any = null;

  componentDidMount() {
    this.componentDomRef = ReactDOM.findDOMNode(this);
  }

  onCardSettingsOpenCloseAnimation = () => {
    const prevStateNodeSummaryOpen = this.state.isNodeSummaryOpen;
    const prevStateNodeSettingsloaded = this.state.isNodeSettingsloaded;
    this.setState({
      isNodeSummaryOpen: !prevStateNodeSummaryOpen,
      isNodeSettingsloaded: !prevStateNodeSettingsloaded
    });
  };

  onNodeSettingsloaded = () => {
    this.setState({isNodeSettingsloaded: true});
  };

  getTranslationDistance = (index: number) => {
    return this.componentDomRef.getBoundingClientRect().top - 155; //todo: magic number please refactor
  };

  closeOnOverlayClick = () => {
    this.setState({isNodeSummaryOpen: false});
  };

  render() {
    const {isNodeSummaryOpen, isNodeSettingsloaded} = this.state;
    return (
      <>
        {isNodeSummaryOpen && (
          <ReactCSSTransitionGroup
            transitionEnterTimeout={500}
            transitionLeaveTimeout={300}
            transitionName="overlay"
          >
            <div onClick={this.closeOnOverlayClick} className={css.overlay} />
          </ReactCSSTransitionGroup>
        )}
        {this.props.children({
          onCardSettingsOpenCloseAnimation: this
            .onCardSettingsOpenCloseAnimation,
          onNodeSettingsloaded: this.onNodeSettingsloaded,
          isNodeSummaryOpen,
          isNodeSettingsloaded,
          getTranslationDistance: this.getTranslationDistance
        })}
      </>
    );
  }
}
