import * as React from "react";
import css from "./ApplicationTile.module.scss";
import Modal from "react-responsive-modal";
import {DashboardContext} from "../../../Providers";
import {Portal} from "../../../Portal";
import {UUID} from "../../../../types/internal-types";

export interface ApplicationTileProps {
  uuid: UUID;
  name: string;
  description: string;
  appThumbnail: string;
  viewportSize: {
    width: number;
    height: number;
  };
}

const initialApplicationTileState = {
  isMkAppOpen: false,
  html: "",
  scripts: ""
};

export class ApplicationTile extends React.Component<
  ApplicationTileProps,
  typeof initialApplicationTileState
> {
  readonly state = initialApplicationTileState;
  static contextType = DashboardContext;
  context!: React.ContextType<typeof DashboardContext>;

  renderThumbnail = () => {
    const {description} = this.props;
    return (
      <div className={css.applicationThumbnailWrapper}>
        {description ? (
          <div className={css.textThumbnailLarge}>
            {description.charAt(0).toUpperCase()}
          </div>
        ) : (
          <div className={css.textThumbnail}>MKapp</div>
        )}
      </div>
    );
  };

  openMKAppMainView = async () => {
    const {
      html: [htmlStream],
      scripts: [scriptStream]
    } = await this.context.dashboardServerApi.getAppData(this.props.uuid);
    this.setState({isMkAppOpen: true, html: htmlStream, scripts: scriptStream});
  };

  closeMKAppMainView = () => {
    this.setState({isMkAppOpen: false});
  };

  render() {
    const {isMkAppOpen} = this.state;
    const {description} = this.props;
    const last6chars = this.props.uuid.substring(
      this.props.uuid.length - 6,
      this.props.uuid.length
    );
    return (
      <div
        onDoubleClick={this.openMKAppMainView}
        className={`
          ${css.applicationWrapper} 
          ${css.smartHome}`}
      >
        <div className={css.applicationTitle}>
          {this.renderThumbnail()}
          {description ? description : `...${last6chars}`}
        </div>
        <div className={css.applicationContent}>
          {isMkAppOpen ? (
            <Modal
              closeOnEsc
              classNames={{modal: css.mkAppModalWrapper}}
              onClose={this.closeMKAppMainView}
              open={this.state.isMkAppOpen}
            >
              <Portal html={this.state.html} scripts={this.state.scripts} />
            </Modal>
          ) : (
            <div>put knobs here</div>
          )}
        </div>
      </div>
    );
  }
}
