import {thumbnail} from "../../serverApiMocks";

import {
  NodeList,
  GetUserNodeListDTO,
  UUID,
  OnGetNodeInfoCallbackDTO,
  NoteDetails,
  NodeSettingsDTO,
  NodeSettings
} from "../../types/internal-types";

export class DashboardServerApi {
  makeSenseApi = window.MkSAPIBuilder.GetInstance();

  constructor() {
    this.makeSenseApi.ConnectGateway(() =>
      console.log("Dashboard connected to gateway")
    );
    this.makeSenseApi.Gateway.OnGatewayDataArrivedCallback = (data: any) => {};
    this.makeSenseApi.Gateway.OnGatewayAdminCallback = (data: any) => {
      console.log("OnGatewayAdminCallback", data);
    };
  }

  getApplicationThumbnail = () => {
    console.log("/applications/:appId:/thumbnail");
    return Promise.resolve(thumbnail);
  };

  getNodeDetails = (uuid: UUID) => {
    const nodeInfoPromise = new Promise<NoteDetails>((resolve) => {
      this.makeSenseApi.GetNodeInfo(
        uuid,
        (response: OnGetNodeInfoCallbackDTO, _error: any) => {
          const payload = response && response.data && response.data.payload;
          resolve(payload);
        }
      );
    });
    return nodeInfoPromise;
  };

  getNodeSettings = (uuid: UUID): Promise<NodeSettings> => {
    return Promise.all([
      this.getNodeHtmlSettings(uuid),
      this.getNodeScriptsSettings(uuid)
    ]).then((nodeSettingsFiles) => {
      const [html, scripts] = nodeSettingsFiles;
      return {html: [html], scripts: [scripts], css: []} as any;
    });
  };

  getAppData = (uuid: UUID): Promise<any> => {
    return Promise.all([this.getAppHtml(uuid), this.getAppScripts(uuid)]).then(
      (data) => {
        const [html, scripts] = data;
        return {html: [html], scripts: [scripts], css: []} as any;
      }
    );
  };

  private getNodeScriptsSettings = (uuid: UUID) => {
    const promise = new Promise<string>((resolve) => {
      this.makeSenseApi.GetFileContent(
        uuid,
        {
          ui_type: "config",
          file_name: "",
          file_type: "js"
        },
        (response: NodeSettingsDTO) => {
          resolve(response.data.payload.content);
        }
      );
    });
    return promise;
  };

  private getAppHtml = (uuid: UUID) => {
    const promise = new Promise<string>((resolve) => {
      this.makeSenseApi.GetFileContent(
        uuid,
        {
          ui_type: "app",
          file_name: "",
          file_type: "html"
        },
        (response: NodeSettingsDTO) => {
          resolve(response.data.payload.content);
        }
      );
    });
    return promise;
  };

  private getAppScripts = (uuid: UUID) => {
    const promise = new Promise<string>((resolve) => {
      this.makeSenseApi.GetFileContent(
        uuid,
        {
          ui_type: "app",
          file_name: "",
          file_type: "js"
        },
        (response: NodeSettingsDTO) => {
          resolve(response.data.payload.content);
        }
      );
    });
    return promise;
  };

  private getNodeHtmlSettings = (uuid: UUID) => {
    const promise = new Promise<string>((resolve) => {
      this.makeSenseApi.GetFileContent(
        uuid,
        {
          ui_type: "config",
          file_name: "",
          file_type: "html"
        },
        (response: NodeSettingsDTO) => {
          resolve(response.data.payload.content);
        }
      );
    });
    return promise;
  };

  getNodeList = () => {
    const deviceListDataPromise = new Promise<NodeList[]>((resolve, reject) => {
      this.makeSenseApi.Webface.GetUserNodeList(
        (response: GetUserNodeListDTO) => {
          resolve(response.nodes.data);
          if (response && response.error) {
            reject("OnGatewayDataArrivedCallback received an error");
          }
        }
      );
    });
    return deviceListDataPromise;
  };
}
