import * as React from "react";
import {DashboardContext} from "../../../Providers";
import css from "./NodeSettings.module.scss";
import {UUID} from "../../../../types/internal-types";
import {Portal} from "../../../Portal";
import {CloseButton} from "../../../IconWrappers/CloseButton";

interface OwnProps {
  deviceUuid: UUID;
  nodeName: string;
  closeModal: () => void;
  onCardSettingsLoaded?: () => void;
}

const ownState = {
  html: [""],
  scripts: [""],
  isLoadingFiles: true
};

export class NodeSettings extends React.Component<OwnProps, typeof ownState> {
  readonly state = ownState;

  static contextType = DashboardContext;
  context!: React.ContextType<typeof DashboardContext>;

  async componentDidMount() {
    const nodeSettings = await this.context.dashboardServerApi.getNodeSettings(
      this.props.deviceUuid
    );
    this.setState({...nodeSettings, isLoadingFiles: false}, () => {
      this.props.onCardSettingsLoaded && this.props.onCardSettingsLoaded();
    });
  }

  render() {
    return (
      <>
        <div key="node-settings" className={css.nodeSettingsHeader}>
          <div>
            NODE SETTINGS
            <span className={css.nodeSettingsHeaderDetails}>
              {this.props.nodeName}
            </span>
          </div>
          <div onClick={this.props.closeModal}>
            <CloseButton className={css.closeButton} />
          </div>
        </div>
        <div className={css.nodeSettingsContent}>
          {this.state.isLoadingFiles ? (
            <div className={css.loading}>
              <div className={css.nodeSettingsSpinner} /> <div> Loading...</div>
            </div>
          ) : (
            <Portal scripts={this.state.scripts[0]} html={this.state.html[0]} />
          )}
        </div>
      </>
    );
  }
}
