import {PageHeader} from "../PageHeader";
import * as React from "react";
import css from "./NodePage.module.scss";
import {NodeCardSummary} from "./NodeCardSummary";
import {DashboardContext} from "../../Providers";
import {NodeList} from "../../../types/internal-types";
import {NodeCardTransition} from "./NodeCardTransition/NodeCardTransition";
import classNames from "classnames";
import {NodeCardWrapper} from "./NodeCardWrapper/NodeCardWrapper";
import {NodeSettings} from "./NodeSettings/NodeSettings";

export interface OwnProps {}

const innitialState = {
  showNodeList: false,
  nodeList: [] as NodeList[],
  filteredMaster: [] as NodeList[],
  isDisplayWithMasterNodes: true
};

export class NodesPage extends React.Component<OwnProps, typeof innitialState> {
  readonly state = innitialState;
  static contextType = DashboardContext;
  context!: React.ContextType<typeof DashboardContext>;

  async componentDidMount() {
    const data: NodeList[] = await this.context.dashboardServerApi.getNodeList();
    this.setState({nodeList: data});
  }

  render() {
    const {nodeList} = this.state;

    return (
      <>
        <section className={css.pageWrapper}>
          <PageHeader>
            <div className={css.pageHeaderWrapper}>MY DEVICES</div>
          </PageHeader>
          <div className={css.pageContentWrapper}>
            {nodeList.map((nodeData, index) => (
              <NodeCardTransition key={index}>
                {({
                  onCardSettingsOpenCloseAnimation,
                  isNodeSummaryOpen,
                  onNodeSettingsloaded,
                  getTranslationDistance,
                  isNodeSettingsloaded
                }) => {
                  return (
                    <NodeCardWrapper uuid={nodeData.uuid}>
                      {({isLoading, deviceSummaryData}) => {
                        return (
                          <>
                            {deviceSummaryData && (
                              <div className={css.outerCardWrapper}>
                                <section
                                  style={
                                    isNodeSummaryOpen
                                      ? {
                                          transform: `translateY(-${getTranslationDistance(
                                            index
                                          )}px`
                                        }
                                      : {}
                                  }
                                  className={classNames({
                                    [css.cardWrapper]: true,
                                    [css.cardWrapperSelected]: isNodeSummaryOpen,
                                    [css.cardWrapperSettingsLoaded]: isNodeSettingsloaded
                                  })}
                                >
                                  <>
                                    {isNodeSummaryOpen ? (
                                      <NodeSettings
                                        nodeName={`${
                                          deviceSummaryData.name
                                        } - ${deviceSummaryData.description}`}
                                        deviceUuid={deviceSummaryData.uuid}
                                        onCardSettingsLoaded={
                                          onNodeSettingsloaded
                                        }
                                        closeModal={
                                          onCardSettingsOpenCloseAnimation
                                        }
                                      />
                                    ) : (
                                      <NodeCardSummary
                                        key={index}
                                        deviceSummaryData={deviceSummaryData}
                                        isLoading={isLoading}
                                        onCardSettingsClickCallback={
                                          onCardSettingsOpenCloseAnimation
                                        }
                                      />
                                    )}
                                  </>
                                </section>
                              </div>
                            )}
                          </>
                        );
                      }}
                    </NodeCardWrapper>
                  );
                }}
              </NodeCardTransition>
            ))}
          </div>
        </section>
      </>
    );
  }
}
