import {MkAppDescription} from "../../types/internal-types";
import * as React from "react";
import {ApplicationTileProps} from "../DashboardPages/ApplicationsPage/ApplicationTile";
import {DashboardContext} from "../Providers";

interface AppLoaderProps {
  mkAppInitData: MkAppDescription;
  render: (loadedMkAppProps: ApplicationTileProps) => React.ReactNode;
}

const innitialState = {
  appThumbnail: null as any,
  mkAppLoadedData: {} as any,
  appProps: {} as any,
  description: ""
};

export class AppLoader extends React.Component<
  AppLoaderProps,
  typeof innitialState
> {
  readonly state = innitialState;
  static contextType = DashboardContext;
  context!: React.ContextType<typeof DashboardContext>;

  async componentDidMount() {
    //there shouls be cacsh managment here, so that Apploader wont fetch app list every time
    try {
      const appThumbnail = await this.context.dashboardServerApi.getApplicationThumbnail();
      const nodeDetails = await this.context.dashboardServerApi.getNodeDetails(
        this.props.mkAppInitData.uuid
      );
      this.setState({
        appThumbnail,
        description: nodeDetails && nodeDetails.description
      });
    } catch (error) {
      console.error(error);
    }
  }

  render() {
    return (
      <>
        {this.props.render({
          appThumbnail: this.state.appThumbnail,
          description: this.state.description,
          ...this.props.mkAppInitData
        })}
      </>
    );
  }
}
