import * as React from "react";
import css from "./NodeCardSummary.module.scss";
import {NoteDetails} from "../../../../types/internal-types";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faBars, faCodeBranch} from "@fortawesome/free-solid-svg-icons";
import {DashboardContext} from "../../../Providers";
import {SkeletonShimmer} from "../../../SkeletonShimmer";
import {Subtitle, CardText} from "./NodeCardTypography";

export interface OwnProps {
  isLoading: boolean;
  deviceSummaryData: NoteDetails;
  onCardSettingsClickCallback?: () => void;
}

interface OwnState {
  isNodeSettingsLoaded: boolean;
}

export class NodeCardSummary extends React.Component<OwnProps, OwnState> {
  static contextType = DashboardContext;
  readonly state = {
    isNodeSettingsLoaded: true,
    isNodeSettingsOpenned: false
  };
  context!: React.ContextType<typeof DashboardContext>;

  handleNodeSettingsClick = () => {
    this.props.onCardSettingsClickCallback &&
      this.props.onCardSettingsClickCallback();
  };

  deviceTypeSection = () => (
    <div
      className={`${css.Section} ${css.deviceContentWrapper} ${css.deviceType}`}
    >
      <div className={css.subTitle}>
        <Subtitle showSkeleton={this.props.isLoading}>DEVICE TYPE</Subtitle>
      </div>
      <CardText showSkeleton={this.props.isLoading}>
        {this.props.deviceSummaryData.type}
      </CardText>
    </div>
  );

  deviceNameSection = () => {
    const {
      deviceSummaryData: {description, name}
    } = this.props;
    const {isLoading} = this.props;
    return (
      <div
        className={`${css.Section} ${css.deviceContentWrapper} ${
          css.deviceName
        }`}
      >
        <div className={css.subTitle}>
          <Subtitle showSkeleton={isLoading}>DEVICE NAME</Subtitle>
        </div>
        <div className={css.text}>
          <CardText showSkeleton={isLoading}>
            {name} - {description}
          </CardText>
        </div>
      </div>
    );
  };

  deviceIdSection = () => {
    return (
      <div
        className={`${css.Section} ${css.deviceContentWrapper} ${
          css.deviceName
        }`}
      >
        <div className={css.subTitle}>
          <Subtitle showSkeleton={this.props.isLoading}>DEVICE ID</Subtitle>
        </div>
        <div className={css.text}>
          <CardText showSkeleton={this.props.isLoading}>
            {this.props.deviceSummaryData.uuid}
          </CardText>
        </div>
      </div>
    );
  };

  deviceConnectionSection = () => {
    return (
      <div
        className={`${css.Section} ${css.deviceContentWrapper} ${
          css.deviceStatus
        }`}
      >
        <div className={css.subTitle}>
          <Subtitle showSkeleton={this.props.isLoading}>STATUS</Subtitle>
        </div>
        <div className={css.text}>
          <CardText showSkeleton={this.props.isLoading}>Connected</CardText>
        </div>
      </div>
    );
  };

  render() {
    const {isLoading} = this.props;
    return (
      <>
        <div className={css.cardSummary}>
          <div className={`${css.Section} ${css.IconSectionBackground}`}>
            <FontAwesomeIcon icon={faCodeBranch} size="2x" color="#768093" />
          </div>
          {this.deviceTypeSection()}
          {this.deviceNameSection()}
          {this.deviceIdSection()}
          {this.deviceConnectionSection()}
          <div
            className={`${css.deviceContentWrapper} ${css.deviceSettingsMenu}`}
          >
            <span
              className={css.deviceSettingsMenuIcon}
              onClick={this.handleNodeSettingsClick}
            >
              <FontAwesomeIcon icon={faBars} size="2x" color="#768093" />
            </span>
          </div>
          {isLoading && (
            <SkeletonShimmer
              width="1400px"
              color="rgba(249, 250, 251, 0.001)"
            />
          )}
        </div>
      </>
    );
  }
}
