import * as React from "react";
import {DashboardContext} from "../../../Providers";
import {UUID, NoteDetails} from "../../../../types/internal-types";

interface OwnProps {
  uuid: UUID;
  children: ({
    isLoading,
    deviceSummaryData
  }: {
    isLoading: boolean;
    deviceSummaryData: NoteDetails | null;
  }) => React.ReactNode;
}

const ownState = {
  isLoading: true,
  deviceSummaryData: {} as NoteDetails | null
};

export class NodeCardWrapper extends React.Component<
  OwnProps,
  typeof ownState
> {
  readonly state = ownState;

  static contextType = DashboardContext;
  context!: React.ContextType<typeof DashboardContext>;

  async componentDidMount() {
    const deviceSummaryData = await this.context.dashboardServerApi.getNodeDetails(
      this.props.uuid
    );
    this.setState({deviceSummaryData, isLoading: false});
  }

  render() {
    const {deviceSummaryData, isLoading} = this.state;
    return (
      <>
        {this.props.children({
          deviceSummaryData,
          isLoading
        })}
      </>
    );
  }
}
