import * as React from "react";
import ReactDOM from "react-dom";
import {DashboardContext} from "../Providers";

interface OwnProps {
  html: string;
  scripts: string;
}
const ownState = {};

export class Portal extends React.Component<OwnProps, typeof ownState> {
  static contextType = DashboardContext;
  context!: React.ContextType<typeof DashboardContext>;
  readonly state = ownState;
  private iframeRoot = React.createRef<HTMLIFrameElement>();

  componentDidMount() {
    this.createIframeContent();
  }

  decodeDomProperty(hexxStream: string) {
    const hexString = hexxStream.toString(); //force conversion
    let parstedString = "";
    for (
      let i = 0;
      i < hexString.length && hexString.substr(i, 2) !== "00";
      i += 2
    )
      parstedString += String.fromCharCode(
        parseInt(hexString.substr(i, 2), 16)
      );
    return parstedString;
  }

  createIframeContent() {
    if (this.iframeRoot.current) {
      const htmlString = this.decodeDomProperty(this.props.html);
      const script = this.decodeDomProperty(this.props.scripts);
      const iframeWrapper: any = ReactDOM.findDOMNode(this.iframeRoot.current);

      if (iframeWrapper && iframeWrapper.contentDocument) {
        iframeWrapper.contentDocument.open();
        iframeWrapper.contentDocument.write(htmlString);
        iframeWrapper.contentDocument.write(`<script>${script}</script>`);
        iframeWrapper.contentDocument.close();
      }
    }
  }

  render() {
    return (
      <>
        <iframe
          title="mk-portal"
          style={{display: "block", width: "100%", height: "100%"}}
          frameBorder="0"
          ref={this.iframeRoot}
        />
      </>
    );
  }
}
